import React, { useState } from 'react';
import Shop from './ShopPage/Shop';
import Storage from './ShopPage/Storage';
import SwitchPanel from './SwitchPanel';


const ShopPage = () => {
    const [isStorage, setisStorage] = useState(false);

    const showShop = () => {
        setisStorage(false);
    };

    const showStorage = () => {
        setisStorage(true);
    };

    return (
        <div>
            <SwitchPanel 
                leftButtonName={'Магазин'}
                leftButtonLink={showShop}
                rightButtonName={'Покупки'}
                rightButtonLink={showStorage}
            />
            { isStorage ? (
                <Storage/>
            ) : (
                <Shop />
            )}
        </div>
    );
};

export default ShopPage;