import React, { useState } from 'react';
import Tasks from './TaskPage/Tasks';
import DoneTasks from './TaskPage/DoneTasks';
import SwitchPanel from './SwitchPanel';
import Price from '../components/Price.js';
import '../styles/task.css'
import { ReactComponent as BackIcon } from '../icon/XGrey.svg';

const TaskPage = () => {
    const [isTasks, setIsTasks] = useState(true);
    const [selectedTask, setSelectedTask] = useState(null);

    const showTasks = () => {
        setIsTasks(true);
        setSelectedTask(null);
    };

    const showDoneTasks = () => {
        setIsTasks(false);
        setSelectedTask(null);
    };

    const handleTaskClick = (task) => {
        setSelectedTask(task);
    };

    const handleBackClick = () => {
        setSelectedTask(null);
    };

    return (
        <div>
            <SwitchPanel 
                leftButtonName={'Задания'}
                leftButtonLink={showTasks}
                rightButtonName={'Выполнено'}
                rightButtonLink={showDoneTasks}
            />
            <div className='boxWindow'>
                {selectedTask ? (
                        <div className='tapTask'>
                            <div className='backIcon'>
                                <BackIcon alt="Назад" onClick={handleBackClick} />
                            </div>
                            <div>
                                <h2 id='taskName'>{selectedTask.TaskName}</h2>
                                <br/>
                                <br/>
                                <div id='taskText'>
                                    <span>{selectedTask.Description}</span>
                                    <br/>
                                    <br/>
                                    <span>{selectedTask.Instructions}</span>
                                </div>
                            </div>
                            <div className='bottom_panel'>
                                <Price price={selectedTask.Payment} />
                                <br/>
                                <button className='reportButton' onClick={handleBackClick}>
                                    <span>Загрузить отчётность</span>    
                                </button>
                            </div>
                        </div>
                    
                ) : isTasks ? (
                    <Tasks onTaskClick={handleTaskClick} />
                ) : (
                    <DoneTasks />
                )}
            </div>
        </div>
    );
};

export default TaskPage;
