import React, { Component } from 'react';
import '../styles/leaderboardPage.css';
import config from './responce';
import axios from 'axios';

class Leaderboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
      currentUser: null,
      currentUserIndex: null,
    };
  }

  componentDidMount() {
    this.fetchUsers();
  }

  async fetchUsers() {
    try {
      const response = await axios.get(config.getTopUsers());
      const users = response.data;
      const currentUserIndex = users.findIndex(user => user.user_id === config.userId);
      const currentUser = currentUserIndex !== -1 ? users[currentUserIndex] : null;
      this.setState({ users, currentUser, currentUserIndex });
    } catch (error) {
      console.error('Failed to fetch users', error);
    }
  }

  render() {
    const { users, currentUser, currentUserIndex } = this.state;

    return (
      <div className='leaderboardBox'>
        <h1 className='leaderboardText'>Рейтинг волонтеров</h1>
        <div className='leaderboard'>
          <div className='player'>
            <div id='playerNumber'>№</div>
            <div id='playerWalletNumber'>Идентификатор</div>
            <div id='playerBalance'>Очки</div>
          </div>
          <div className="table_users">
            {users.map((user, index) => (
              <div
                key={index}
                className="player"
                style={{
                  backgroundColor: user.user_id === config.userId ? 'red' : 'transparent',
                }}
              >
                <div id='playerNumber'>{index + 1}</div>
                <div id='playerWalletNumber'>{user.user_id}</div>
                <div id='playerBalance'>{user.score}</div>
              </div>
            ))}
          </div>
          {currentUser && currentUserIndex !== null && (
            <div className='player'>
              <div id='playerNumber'>{currentUserIndex + 1}</div>
              <div id='playerWalletNumber'>{currentUser.user_id}</div>
              <div id='playerBalance'>{currentUser.score}</div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default Leaderboard;
