import React from 'react';
import { TonConnectButton } from '@tonconnect/ui-react';
import { ReactComponent as Logo } from '../images/notification.svg';
import axios from 'axios';
import config from './responce';
import Price from './Price';

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            score: null,
        };
        this.incrementScore = this.incrementScore.bind(this);
    }

    componentDidMount() {
        this.fetchScore();
    }

    async fetchScore() {
        try {
            const response = await axios.get(config.getScore(), {
                params: {
                    user_id: config.userId,
                },
            });
            this.setState({ score: response.data.score });
        } catch (error) {
            console.error('Failed to fetch score', error);
        }
    }

    incrementScore() {
      this.setState(prevState => ({ score: prevState.score + 1 }));
    } 

    render() {
        const { showNotification } = this.props;
        const { score } = this.state;

        return (
            <header>
                <div>
                    <Price price = {score}/>
                </div>
                    <TonConnectButton ></TonConnectButton>
                <div>
                    <Logo className="notification" alt="Уведомление" onClick={showNotification} />
                </div>
            </header>
        );
    }
}

export default Header;
