import {useTonConnectUI, useTonWallet} from "@tonconnect/ui-react";
import React from 'react';

export const WalletButton = () => {
    const [tonConnectUi] = useTonConnectUI();
    const wallet = useTonWallet();

    return (
    <>
        <div className="backBlock"></div>
        <div className="wallet_connect">
            <div className="_padding-0 _direction-column">
                <div className="_title">Добро пожаловать в NFTree</div>
                <div className="_description">Привяжите свой web3 кошелёк, чтобы продолжить</div>
                <div className="_link"><a href="https://app.tonkeeper.com">Создать кошелёк</a></div>
                <button className="_connect_button" onClick={() => tonConnectUi.openModal()}>
                    {wallet}
                </button>
            </div>
        </div>
    </>
    );
    };

export default WalletButton;