import React, { Component } from 'react';
import Price from '../../components/Price.js';
import axios from 'axios';
import config from './../responce';
class Tasks extends Component {
  state = {
    tasks: []
  };

  componentDidMount() {
    axios.get(config.getTasks())
      .then(response => {
        this.setState({ tasks: response.data });
      })
      .catch(error => {
        console.error('Error fetching tasks:', error);
      });
  }

  tapTask = (task) => {
    // Handle the task click event
    console.log('Task clicked:', task);
    // You can add your custom logic here
  };

  render() {
    return (
        <div>
            <div className='task_active'>
                  {this.state.tasks.map((task, index) => (
                        <div
                          onClick={() => this.props.onTaskClick(task)}
                          key={index}
                          className="taskBox task_not_active">
                          <span id='taskName'>{task.TaskName}</span>
                          <Price price={task.Payment}/>
                        </div>
                  ))}
            </div>
        </div>
    );
  }
}

export default Tasks;